import { Bread } from 'Bread'
import React, { PureComponent } from 'react'
import { Form as FormBox, message, Modal } from 'antd'
import Form from './components/./Form'
import Table from './components/./Table'
import Tool from './components/./Tool'
import { connect } from 'react-redux'
import { withRouter } from 'react-router'
import { mapDispatchToProps } from './redux/mapDispatchToProps'
const confirm = Modal.confirm
@connect(
  (state) => ({
    ...state.batchReceiveAndReturnMaterial,
    menuPermissionIds: state.menuPermissionIds
  }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor(props) {
    super(props)
  }
  componentWillUnmount() {
    this.props.update({
      searchResult: [],
      searchParams: {},
      productCodeOptions: {}
    })
  }
  dadaFormater(formValues, selectedRowKeys, searchParams) {
    let dataParams = {}
    const { craftAbilityList = [] } = formValues
    const { productLineCode, batchNumber, produceTime } = searchParams || {}
    const materialRequestDetails = []
    const craftAbilityListVal =
      craftAbilityList && Object.values(craftAbilityList)
    craftAbilityListVal &&
      craftAbilityListVal.length &&
      craftAbilityListVal.map((item, index, arr) => {
        const { qty = '', maID = '' } = item
        return (
          selectedRowKeys &&
          selectedRowKeys.length &&
          selectedRowKeys.map((rowId, rowIndex, selectRow) => {
            if (maID == rowId) {
              return materialRequestDetails.push({
                maID,
                qty
              })
            }
          })
        )
      })
    dataParams = {
      materialRequestDetails,
      productTime: produceTime,
      productLineCode: (productLineCode && productLineCode.value) || '',
      batchNumber: (batchNumber && batchNumber.value) || ''
    }
    return dataParams
  }
  downloadError = (res) => {
    location.href = res
  }
  /* 报错导出 */
  handleExportError = (res) => {
    let _this = this
    if (res.result && res.result.url) {
      confirm({
        title: '温馨提示',
        // content: `${res.result && res.result.message}`,
        okText: '报错导出',
        cancelText: '我知道了',
        onOk() {
          _this.downloadError(res.result.url)
        },
        onCancel() {}
      })
    } else {
      message.success('操作成功')
    }
  }
  // 领料退料的操作
  handleOperate = (formValues, searchParams) => {
    const {
      handleGetList,
      page,
      size,
      selectedRowKeys,
      handleReturnOption,
      handleGetOption,
      update
    } = this.props
    const operationTypeValue =
      searchParams &&
      searchParams.operationType &&
      searchParams.operationType.value
    const dataParams = this.dadaFormater(
      formValues,
      selectedRowKeys,
      searchParams
    )
    const btnStr =
      operationTypeValue === '1'
        ? '领料'
        : operationTypeValue === '2'
        ? '退料'
        : '领料'
    const _self = this
    confirm({
      title: '温馨提示',
      content: `是否进行${btnStr}操作？`,
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        operationTypeValue === '1' // 领料
          ? handleGetOption(
            {
              data: dataParams
            },
              (data) => {
                try {
                  console.log(data, 1111, _self)
                  console.log(_self.handleExportError)
                  _self.handleExportError(data)
                  update({ selectedRowKeys: [], searchResult: [] })
                  handleGetList({
                    searchParams: { page, size, ...searchParams }
                  })
                } catch (error) {
                  console.log('error is', error)
                }
              }
            )
          : handleReturnOption(
              // 退料
            {
              data: dataParams
            },
              (data) => {
                console.log(data, 2222)
                _self.handleExportError(data)
                update({ selectedRowKeys: [], searchResult: [] })
                handleGetList({ searchParams: { page, size, ...searchParams } })
              }
            )
      }
    })
  }
  handleSubmit = (e) => {
    const { form, searchParams = {}, selectedRowKeys = [] } = this.props
    e.preventDefault()
    if (!(selectedRowKeys && selectedRowKeys.length > 0)) {
      return message.error('没有可以操作的选项')
    }
    if (selectedRowKeys && selectedRowKeys.length > 50) {
      return message.error('单次操作的数据不能超过五十条')
    }
    try {
      form.validateFieldsAndScroll((err, values) => {
        let errSelected = false
        if (err) {
          // 校验不过的选项， 在选中状态下阻止提交
          const errIdArr = Object.keys(err.craftAbilityList)
          errSelected =
            selectedRowKeys && selectedRowKeys.some((r) => errIdArr.includes(r))
        }
        if (errSelected) {
          const operationTypeValue =
            searchParams &&
            searchParams.operationType &&
            searchParams.operationType.value
          const messageStr =
            operationTypeValue === '1'
              ? '领料'
              : operationTypeValue === '2'
              ? '退料'
              : '领料'
          // return message.error(`${messageStr}量须为正数且不超过两位小数`)
          return message.error(`请检查${messageStr}量输入是否正确`)
        } else if (!errSelected) {
          this.handleOperate(values, searchParams)
        }
      })
    } catch (error) {
      console.log('error ', error)
    }
  }
  render() {
    const { history, menuPermissionIds } = this.props
    return (
      <section>
        <Bread route={['生产中心', '批量领退料']} />
        <Form menuPermissionIds={menuPermissionIds} />
        <FormBox onSubmit={this.handleSubmit} autoComplete="off">
          <Tool
            menuPermissionIds={menuPermissionIds}
            history={history}
            form={this.props.form}
          />
          <Table history={history} form={this.props.form} />
        </FormBox>
      </section>
    )
  }
}
export default FormBox.create()(withRouter(App))



