import * as actionType from './actionType'
import { getData } from 'util'
import {
  API_BATCHRECEIVEANDRETURNMATERIALS_PAGE,
  API_BATCHRECEIVEANDRETURNMATERIALS_DET_SOURCE,
  API_BATCHRECEIVEANDRETURNMATERIALS_RETURNMAL,
  API_BATCHRECEIVEANDRETURNMATERIALS_GETMAL,
  API_BATCHRECEIVEANDRETURNMATERIALS_EXPORT
} from 'api'
//同步
export const update = object => {
  console.log('object is ', object)
  return {
    type: actionType.BATCHRECEIVEANDRETURNMATERIAL_UPDATE,
    data: object
  }
}

//异步
// 获取列表
export const handleGetList = reqData => dispatch => {
  dispatch({
    type: actionType.BATCHRECEIVEANDRETURNMATERIAL_UPDATE,
    data: { tableLoading: true }
  })
  const { searchParams = {} } = reqData
  for (let i in searchParams) {
    if (Object.prototype.toString.call(searchParams[i]) === '[object Object]') {
      searchParams[i] = searchParams[i].value
    }
  }
  getData({ api: API_BATCHRECEIVEANDRETURNMATERIALS_PAGE, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      let { page, size } = reqData.searchParams
      dispatch({
        type: actionType.BATCHRECEIVEANDRETURNMATERIAL_UPDATE,
        data: {
          page,
          size,
          tableLoading: false,
          total: data.result.total,
          // searchResult: [...Array(5).keys()] || []
          searchResult: data.result || []
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.BATCHRECEIVEANDRETURNMATERIAL_FAIL
      })
    })
}

// 获取需求明细
export const getSourceDetail = (reqData, callBack) => dispatch => {
  getData({ api: API_BATCHRECEIVEANDRETURNMATERIALS_DET_SOURCE, ...reqData })
    .then(data => {
      callBack && callBack()
    })
    .catch(err => {
      dispatch({
        type: actionType.BATCHRECEIVEANDRETURNMATERIAL_FAIL
      })
    })
}
// 导出
export const handleExport = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.BATCHRECEIVEANDRETURNMATERIAL_UPDATE,
    data: { modalTuttonLoading: true }
  })
  getData({ api: API_BATCHRECEIVEANDRETURNMATERIALS_EXPORT, ...reqData })
    .then(data => {
      callBack && callBack()
      dispatch({
        type: actionType.BATCHRECEIVEANDRETURNMATERIAL_UPDATE,
        data: { modalTuttonLoading: false }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.BATCHRECEIVEANDRETURNMATERIAL_FAIL
      })
    })
}
// 领料
export const handleGetOption = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.BATCHRECEIVEANDRETURNMATERIAL_UPDATE,
    data: { modalTuttonLoading: true }
  })
  console.log('reqData is handleGetOption', reqData)
  getData({ api: API_BATCHRECEIVEANDRETURNMATERIALS_GETMAL, ...reqData })
    .then(data => {
      callBack && callBack(data)
      dispatch({
        type: actionType.BATCHRECEIVEANDRETURNMATERIAL_UPDATE,
        data: { modalTuttonLoading: false }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.BATCHRECEIVEANDRETURNMATERIAL_FAIL
      })
    })
}
// 退料
export const handleReturnOption = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.BATCHRECEIVEANDRETURNMATERIAL_UPDATE,
    data: { modalTuttonLoading: true }
  })
  console.log('reqData is handleReturnOption', reqData)
  getData({ api: API_BATCHRECEIVEANDRETURNMATERIALS_RETURNMAL, ...reqData })
    .then(data => {
      callBack && callBack(data)
      dispatch({
        type: actionType.BATCHRECEIVEANDRETURNMATERIAL_UPDATE,
        data: { modalTuttonLoading: false }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.BATCHRECEIVEANDRETURNMATERIAL_FAIL
      })
    })
}



