import React, { PureComponent } from 'react'
import { Table, Form, Input, Tooltip } from 'antd'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { countTableScroll } from 'util'
import moment from 'moment'
import style from './style/Table.scss'
const FormItem = Form.Item
@connect(
  state => ({
    ...state.batchReceiveAndReturnMaterial,
    userLoation: state.userLoation
  }),
  mapDispatchToProps
)
export default class App extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  componentDidMount() {
    const { update } = this.props
    update({
      searchParams: {
        initDate: moment(),
        operationType: { value: '1', children: '领料' },
        produceTime: moment().format('YYYY-MM-DD')
      }
    })
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const { handleGetList, userLoation } = this.props
    if (newProps.userLoation && newProps.userLoation.value != (userLoation && userLoation.value)) {
      handleGetList({ searchParams: { page: 1, size: newProps.size, ...newProps.searchParams } })
    }
  }
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const { handleGetList, searchParams, page, size } = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: true,
            columns: sorter.field
          }
        })
      } else {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: false,
            columns: sorter.field
          }
        })
      }
    } else {
      // 判断是点跳页面还是排序
      let reqObj = { ...searchParams }
      for (let i in reqObj) {
        if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
          reqObj[i] = reqObj[i].value
        }
      }
      handleGetList({
        searchParams: {
          page: pagination.current,
          size: pagination.pageSize,
          ...reqObj
        }
      })
    }
  }
  regFormatter(unit) {
    let regPattern = '',
      regMsg = ''
    // 正整数
    const intValidate = /^[0-9]\d*$/
    // 正数且不超过两位小数
    const dotValidate = /^(?!0+(?:\.0+)?$)(?:[1-9]\d*|0)(?:\.\d{1,2})?$|(^0$)/    //正数或0
    // const dotValidate = /^(?!0+(?:\.0+)?$)(?:[1-9]\d*|0)(?:\.\d{1,2})?$/
    let unitFormatter = String(unit && unit.toUpperCase())
    if (unitFormatter && (unitFormatter === 'KG' || unitFormatter === 'G')) { // 只有kg或者g的单位，才允许有小数
      regPattern = dotValidate
      regMsg = '支持两位小数'
    } else {
      regPattern = intValidate
      regMsg = '请填写正数或0'
    }
    return {
      regPattern,
      regMsg
    }
  }
  renderTooltipTable(sourceType) {
    let tooltipStr = '',
      titleNode = ''
    if (sourceType === '1') { // 领料
      tooltipStr = '领料量'
      titleNode = <div className={style.titleText}>
        <p>1、领料量默认取值:领料量 = 建议领料量 - 已领料量</p>
        <p>2、业务人员可根据实际情况调整领料量的值</p>
      </div>
    } else if (sourceType === '2') { // 退料
      tooltipStr = '退料量'
      titleNode = <div className={style.titleText}>
        <p>1、退料量默认取值:退料量 = 可退料量</p>
        <p>2、业务人员可根据实际情况调整退料量的值</p>
      </div>
    }
    return (
      <Tooltip
          placement='top'
          title={titleNode}
          trigger='click'
        >
          <span>{tooltipStr}</span>
          <img className={style.img_in_table} src='/image/questionTip.png' />
        </Tooltip>
    )
  }
  renderColumns(sourceType) {
    const {
      form = {}
    } = this.props
    const { getFieldDecorator } = form
    let columns = ''
    let TooltipTable = this.renderTooltipTable(sourceType)
    if (sourceType === '1') {
      columns = [
        {
          title: '商品编码',
          width: 80,
          dataIndex: 'productCode',
          key: 'productCode'
        },
        {
          title: '商品名称',
          width: 120,
          dataIndex: 'productName',
          key: 'productName'
        },
        {
          title: '规格',
          width: 80,
          dataIndex: 'spec',
          key: 'spec'
        },
        {
          title: '单位',
          width: 80,
          dataIndex: 'unit',
          key: 'unit'
        },
        {
          title: '原料库存',
          width: 80,
          dataIndex: 'productStock',
          key: 'productStock'
        },
        {
          title: '建议领料量',
          width: 80,
          dataIndex: 'suggestReceiveQty',
          key: 'suggestReceiveQty'
        },
        {
          title: '已领料量',
          width: 80,
          dataIndex: 'alreadyReceivedQty',
          key: 'alreadyReceivedQty'
        },
        {
          // title: '领料量',
          title: TooltipTable,
          width: 80,
          dataIndex: 'pickingNumber',
          key: 'pickingNumber',
          render: (text, record, index) => {
            const { id = '', unit, productStock = '' } = record
            const { regPattern, regMsg } = this.regFormatter(unit)
            return (
              <div className='box_in_table'>
                <FormItem
                      // hasFeedback
                >
                {getFieldDecorator(`craftAbilityList[${id}][qty]`, {
                  initialValue: text ? text : (text === 0 || text === '0') ? '0' : '',
                  rules: [
                    {
                      validator(rule, value, callback, source, options) {
                        let errors = []
                        if (Number(value) > Number(productStock)) {
                          errors.push(new Error('不能大于库存'))
                          return callback(errors)
                        }
                        if (!value && value !== 0 && value !== '0') {
                          errors.push(new Error('不能为空'))
                          return callback(errors)
                        }
                        if (!(regPattern.test(Number(value)))) {
                          errors.push(new Error(regMsg))
                          return callback(errors)
                        }
                        // callback()
                        callback(errors)
                      }
                    }
                  ]
                })(
                  <Input placeholder={`${regMsg}`}/>
                )}
                </FormItem>
                {
                  id ?
                    <FormItem
                    >
                      {getFieldDecorator(`craftAbilityList[${id}][maID]`, {
                        initialValue: id || ''

                      })(
                        <Input type={'hidden'} />
                      )}
                    </FormItem>
                  : ''
                }
              </div>
            )
          }
        }
      ]
    } else if (sourceType === '2') {
      columns = [
        {
          title: '商品编码',
          width: 80,
          dataIndex: 'productCode',
          key: 'productCode'
        },
        {
          title: '商品名称',
          width: 120,
          dataIndex: 'productName',
          key: 'productName'
        },
        {
          title: '规格',
          width: 80,
          dataIndex: 'spec',
          key: 'spec'
        },
        {
          title: '单位',
          width: 80,
          dataIndex: 'unit',
          key: 'unit'
        },
        {
          title: TooltipTable,
          width: 80,
          dataIndex: 'returnNumber',
          key: 'returnNumber',
          render: (text, record, index) => {
            const { id = '', unit } = record
            const { regPattern, regMsg } = this.regFormatter(unit)
            return (
              <div className='box_in_table'>
                <FormItem
                      // hasFeedback
                >
                {getFieldDecorator(`craftAbilityList[${id}][qty]`, {
                  initialValue: text ? text : (text === 0 || text === '0') ? '0' : '',
                  rules: [
                    {
                      validator(rule, value, callback, source, options) {
                        let errors = []
                        if (!value && value !== 0 && value !== '0') {
                          errors.push(new Error('不能为空'))
                          callback(errors)
                          return errors
                        }
                        if (!(regPattern.test(Number(value)))) {
                          errors.push(new Error(regMsg))
                          callback(errors)
                          return errors
                        }
                        callback(errors)
                      }

                    }
                  ]
                })(
                  <Input placeholder={`${regMsg}`}/>
                )}
                </FormItem>
                {
                  id ?
                    <FormItem
                      // hasFeedback
                    >
                      {getFieldDecorator(`craftAbilityList[${id}][maID]`, {
                        initialValue: id || ''

                      })(
                        <Input type={'hidden'} />
                      )}
                    </FormItem>
                  : ''
                }
              </div>
            )
          }
        }
      ]
    } else {
      columns = []
    }
    return columns
  }
  render() {
    const {
      tableLoading,
      searchParams = {},
      selectedRowKeys,
      searchResult = []
    } = this.props
    const operationTypeValue = searchParams && searchParams.operationType && searchParams.operationType.value
    let rowSelection = {
      selectedRowKeys,
      hideDefaultSelections: true,
      selections: [
        {
          key: 'all-data',
          text: '选中前50条',
          onSelect: (changeableRowKeys) => {
            console.log('changeableRowKeys is', changeableRowKeys)
            let newSelectedRowKeys = []
            newSelectedRowKeys = changeableRowKeys.filter((key, index) => {
              if (index < 50) {
                return true
              }
              return false
            })
            console.log('newSelectedRowKeys is', newSelectedRowKeys)
            this.setState({
              selectedRowKeys: newSelectedRowKeys //前50条
            })
            this.props.update({ selectedRowKeys: newSelectedRowKeys })
          }
        }],

      onChange: (selectedRowKeys, selectedRows) => {
        const { update } = this.props
        update({ selectedRowKeys, selectedRows })
        // update({ selectedRowKeys: [selectedRowKeys[0]], selectedRows: [selectedRows[0]] })
      }
    }
    const columns = this.renderColumns(operationTypeValue) || []
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          columns={columns}
          dataSource={searchResult}
          scroll={{ x: countTableScroll(columns), y: 800 }}
          onChange={this.onChange}
          pagination={false}
          rowSelection={rowSelection}
        />
      </div>
    )
  }
}



