import React, { PureComponent } from 'react'
import { Form, Button, Row, Col, DatePicker } from 'antd'
import style from './style/Form.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { CsxSelect } from 'Form'
import {
  API_BATCHRECEIVEANDRETURNMATERIALS_BATCHLIST_DROP,
  API_BATCHRECEIVEANDRETURNMATERIALS_PRODUCTIONLIST_DROP
} from 'api'
import moment from 'moment'
import { withRouter } from 'react-router'
const FormItem = Form.Item
@connect(
  state => ({ ...state.batchReceiveAndReturnMaterial }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
    this.state = {
      hasselectedAll: false // bool - 控制按钮的disabled
    }
  }
  // 改变当前的输入值
  onInputChange(values) {
    const { searchParams, update } = this.props
    const { source, ...data } = values
    let dataLeft = {}
    console.log('source is', source, values, data)
    if (source === 'operationType') {
      // eslint-disable-next-line no-unused-vars
      const { productLineCode, batchNumber, ...objLeft } = { ...searchParams } //  清空产线和波次的数据
      dataLeft = objLeft
    } else if (source === 'initDate') {
      // eslint-disable-next-line no-unused-vars
      const { productLineCode, batchNumber, ...objLeft } = { ...searchParams } //  清空产线和波次的数据
      dataLeft = objLeft
    } else if (source === 'productLineCode') {
      // eslint-disable-next-line no-unused-vars
      const { batchNumber, ...objLeft } = { ...searchParams } //  清空、波次的数据
      dataLeft = objLeft
    } else if (source === 'batchNumber') {
      dataLeft = { ...searchParams }
    }
    const searchParamsCopy = { ...dataLeft, ...data }
    const hasselectedAll =
      searchParamsCopy &&
      (searchParamsCopy.productLineCode &&
        searchParamsCopy.productLineCode.value &&
        searchParamsCopy.batchNumber &&
        searchParamsCopy.batchNumber.value)
        ? true
        : false
    this.setState({
      hasselectedAll
    })
    return update({
      searchParams: { ...dataLeft, ...data },
      searchResult: [],
      selectedRowKeys: [],
      selectedRows: [],
      hasselectedAll
    })
  }
  // 查询数据
  query = () => {
    const { handleGetList, searchParams, size } = this.props
    let reqObj = { ...searchParams }
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }
    handleGetList({ searchParams: { ...reqObj, page: 1, size } })
  }

  // 重置
  reset = () => {
    const { update, searchParams = {} } = this.props
    const { produceTime = '', operationType, initDate } = searchParams
    update({
      searchParams: { produceTime, operationType, initDate },
      productCodeOptions: {},
      searchResult: [],
      selectedRowKeys: [],
      selectedRows: []
    })
  }
  render() {
    const {
      searchParams = {},
      form: { getFieldDecorator },
      menuPermissionIds
    } = this.props
    const {
      productLineCode,
      batchNumber,
      operationType,
      produceTime,
      initDate
    } = searchParams
    const typeOptions = [
      { key: '1', value: '领料' },
      { key: '2', value: '退料' }
    ]
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 }
      }
    }
    const productLineCodeParams = {
      operationType: operationType && operationType.value,
      produceTime
    }
    const produceBatchParams = {
      operationType: (operationType && operationType.value) || '',
      produceTime,
      productLineCode: (productLineCode && productLineCode.value) || ''
    }
    console.log(this.props, 999999)
    return (
      <Form className={style.form}>
        <Row type='flex' justify='start'>
          <Col span={8}>
            <div className={'commonInput'}>
              <div
                className='boxInSearch'
                style={{ marginRight: 0, width: '100%' }}
              >
                <FormItem {...formItemLayout} label='操作类型'>
                  {getFieldDecorator('operationType', {
                    initialValue: operationType,
                    rules: [
                      {
                        required: true,
                        message: ' '
                      }
                    ]
                  })(
                    <CsxSelect
                      data={typeOptions}
                      // label='操作类型'
                      allowClear={false}
                      selectItem={{
                        data: operationType,
                        key: 'key',
                        valueKey: 'value'
                      }}
                      onChangeSelect={value => {
                        this.onInputChange({
                          operationType: value,
                          source: 'operationType'
                        })
                      }}
                    />
                  )}
                </FormItem>
              </div>
            </div>
          </Col>
          <Col span={8}>
            <div className={'commonInput'}>
              {/* <label>生产日期：</label> */}
              <div
                className='boxInSearch'
                style={{ marginRight: 0, width: '100%' }}
              >
                <FormItem {...formItemLayout} label='生产日期'>
                  {getFieldDecorator('initDate', {
                    initialValue: initDate,
                    rules: [
                      {
                        required: true,
                        message: ' '
                      }
                    ]
                  })(
                    <DatePicker
                      allowClear={false}
                      disabledDate={current =>
                        current &&
                        (current.valueOf() > moment() ||
                          current.valueOf() <
                            moment()
                              .subtract(1, 'days')
                              .startOf('day'))
                      }
                      onChange={time => {
                        let produceTime =
                          time && `${moment(time).format('YYYY-MM-DD')}`
                        this.onInputChange({
                          produceTime,
                          initDate: time,
                          source: 'initDate'
                        })
                      }}
                      format='YYYY-MM-DD'
                    />
                  )}
                </FormItem>
              </div>
            </div>
          </Col>
          <Col span={8}>
            <div className={'commonInput'}>
              <div
                className='boxInSearch'
                style={{ marginRight: 0, width: '100%' }}
              >
                <FormItem {...formItemLayout} label='产线编码'>
                  {getFieldDecorator('productLineCode', {
                    initialValue: productLineCode,
                    rules: [
                      {
                        required: true,
                        message: ' '
                      }
                    ]
                  })(
                    <CsxSelect
                      dateApi={
                        API_BATCHRECEIVEANDRETURNMATERIALS_PRODUCTIONLIST_DROP
                      }
                      // label='产线编码'
                      allowClear={false}
                      searchParams={productLineCodeParams}
                      selectItem={{
                        data: productLineCode,
                        key: 'code',
                        valueKey: ['code', 'name']
                      }}
                      onChangeSelect={value => {
                        this.onInputChange({
                          productLineCode: value,
                          source: 'productLineCode'
                        })
                      }}
                    />
                  )}
                </FormItem>
              </div>
            </div>
          </Col>
          <Col span={8}>
            <div className={'commonInput'}>
              <div
                className='boxInSearch'
                style={{ marginRight: 0, width: '100%' }}
              >
                <FormItem {...formItemLayout} label='生产波次'>
                  {getFieldDecorator('batchNumber', {
                    initialValue: batchNumber,
                    rules: [
                      {
                        required: true,
                        message: ' '
                      }
                    ]
                  })(
                    <CsxSelect
                      dateApi={
                        API_BATCHRECEIVEANDRETURNMATERIALS_BATCHLIST_DROP
                      }
                      // label='生产波次'
                      allowClear={false}
                      searchParams={produceBatchParams}
                      selectItem={{
                        data: batchNumber,
                        key: 'code',
                        valueKey: ['name']
                      }}
                      onChangeSelect={value => {
                        this.onInputChange({
                          batchNumber: value,
                          source: 'batchNumber'
                        })
                      }}
                    />
                  )}
                </FormItem>
              </div>
            </div>
          </Col>
          <Col span={8} push={8}>
            <Row type='flex' justify='end'>
              {menuPermissionIds &&
                menuPermissionIds.includes('batchReceiveAndReturnMaterial_find') && (
                  <Button
                    type='primary'
                    className={style.mr10}
                    onClick={this.query}
                    disabled={!this.state.hasselectedAll}
                  >
                    查询
                  </Button>
                )}
             <Button type='primary' onClick={this.reset}>
                    重置
                  </Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(withRouter(App))



