import React, { PureComponent } from 'react'
import { message, Button } from 'antd'
import style from './style/Tool.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
@connect(
  (state) => ({ ...state.batchReceiveAndReturnMaterial }),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  // 导出
  handleExport = () => {
    const { handleExport, searchParams, page, size } = this.props
    let reqObj = { ...searchParams }
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }
    let objValues = Object.values(reqObj)
    delete reqObj.initDate
    let flag = false
    objValues &&
      objValues.length > 0 &&
      objValues.map((item, index, arr) => {
        if (item) {
          flag = true
        }
        return null
      })
    if (flag) {
      handleExport({ searchParams: { page, size, ...reqObj } }, () => {
        message.success('导出成功')
      })
    } else {
      message.error('未选择、输入部分任何筛选条件')
    }
  }
  // 选中前五十
  handleSelect = () => {
    const { selectedRowKeys = [], update, searchResult = [] } = this.props
    console.log('selectedRowKeys is', selectedRowKeys)
    let newSelectedRowKeys = []
    searchResult.map((item, index) => {
      const { id } = item
      if (index < 50) {
        newSelectedRowKeys.push(id)
      }
    })
    update({ selectedRowKeys: newSelectedRowKeys })
  }
  render() {
    const {
      searchParams = {},
      menuPermissionIds = [],
      modalTuttonLoading
    } = this.props
    const operationTypeValue =
      searchParams &&
      searchParams.operationType &&
      searchParams.operationType.value
    const btnStr =
      operationTypeValue === '1'
        ? '领料'
        : operationTypeValue === '2'
        ? '退料'
        : '领料'
    return (
      <div className={style['table-tool']}>
        <Button
          type="primary"
          className={style.btn}
          onClick={this.handleSelect}
          loading={modalTuttonLoading}
        >
          选中前50条
        </Button>

        {operationTypeValue === '1'
          ? menuPermissionIds &&
            menuPermissionIds.includes(
              'batchReceiveAndReturnMaterial_exportShortage'
            ) && (
              <Button
                type="primary"
                className={style.btn}
                onClick={this.handleExport}
                disabled={!this.props.hasselectedAll}
                loading={modalTuttonLoading}
              >
                导出库存不足商品
              </Button>
            )
          : null}
        {menuPermissionIds &&
          menuPermissionIds.includes(
            'batchReceiveAndReturnMaterial_getMaterial'
          ) && (
            <Button
              type="primary"
              className={style.btn}
              htmlType="submit"
              loading={modalTuttonLoading}
            >
              {btnStr}
            </Button>
          )}
      </div>
    )
  }
}



